<template>
  <li class="menu-item" :class="classList">
    <menu-item-link :item="item" :depth="depth" :floating="floating" @expand="toggleExpand" @open="open({ path, source })" ref="anchor">
      <i v-if="hasIcon" class="menu-icon" :class="item.icon"></i>
      <i v-else-if="hasIcon2 && item.create" class="menu-icon" :class="item.icon2"></i>
      <span class="menu-text">{{ item.title }}</span>
      <span v-if="item.badge" class="badge badge-subtle" :class="item.badge.class || 'badge-alert'">{{ item.badge.text }}</span>
    </menu-item-link>

    <expandable-submenu v-if="shouldShowExpandableMenu"
      :item="item"
      :path="path"
      :depth="depth"
      :source="source"
    ></expandable-submenu>

    <floating-submenu v-if="shouldShowFloatingMenu"
      :item="item"
      :path="path"
      :depth="depth"
      :source="source"
      :reference="anchor"
    ></floating-submenu>
  </li>
</template>

<script>
import MenuItemLink from './MenuItemLink'
import FloatingSubmenu from './FloatingSubmenu'
import ExpandableSubmenu from './ExpandableSubmenu'

import { createNamespacedHelpers } from 'vuex'
const { mapActions, mapState } = createNamespacedHelpers('menu')

export default {
  name: 'menu-item',
  components: {
    MenuItemLink,
    FloatingSubmenu,
    ExpandableSubmenu
  },
  props: {
    item: {
      type: Object,
      required: true
    },
    index: {
      type: Number,
      required: true
    },
    path: {
      type: String,
      default: ''
    },
    depth: {
      type: Number,
      default: 1
    },
    floating: {
      type: Boolean,
      default: false
    },
    source: {
      type: String,
      default: 'items'
    }
  },
  data() {
    return {
      isMounted: false
    }
  },
  mounted() {
    this.isMounted = true
  },
  beforeDestroy() {
    this.isMounted = false
  },
  computed: {
    ...mapState({
      isMobile: state => state.mobile,
      isCollapsed: state => state.collapsed,
    }),
    isOpen() {
      return this.item.open === true
    },
    isActive() {
      return this.item.active === true
    },
    isExpanded() {
      return this.item.expanded === true
    },
    hasChildren() {
      return this.item.children && this.item.children.length > 0
    },
    hasIcon() {
      return this.item.icon && this.item.icon.trim().length > 0
    },
    hasIcon2() {
      return this.item.icon2 && this.item.icon2.trim().length > 0
    },
    anchor() {
      return this.isMounted
        ? this.$refs.anchor.$el
        : null
    },
    shouldShowFloatingMenu() {
      return this.hasChildren &&
        this.anchor &&
        this.isOpen &&
        (
          (this.isCollapsed) ||
          (this.isCollapsed == false && this.isExpanded == false)
        ) &&
        this.isMobile == false;
    },
    shouldShowExpandableMenu() {
      return this.hasChildren &&
      ! this.shouldShowFloatingMenu &&
      ! this.floating
    },
    classList() {
      return {
        'has-open': this.isExpanded,
        'has-active': this.isActive,
        'has-child': this.hasChildren,
      }
    },
  },
  methods: {
    ...mapActions([
      'open', 'expand', 'collapse'
    ]),
    toggleExpand() {
      const path = this.path
      const source = this.source

      this.item.expanded
        ? this.collapse({ path, source })
        : this.expand({ path, source })
    }
  }
}
</script>
