import Vue from 'vue'
import Store from './../store';
import MenuComponent from '../Menu.vue';
import { EventBus } from '../utils/event-bus';

export default class Menu {
  constructor() {
  }

  mount(selector, data = null) {
    const menu = data ? data : JSON.parse(document.querySelector(selector).dataset.menu);
    const store = new Store(menu)
    const app = new Vue({
      store,
      render: h => h(MenuComponent)
    }).$mount(selector)

    const overlay = document.createElement('div')
    overlay.className = 'km_menu_overlay'
    overlay.addEventListener('click', () => this.close())

    this.app = app
    this.data = menu
    this.store = store
    this.overlay = overlay
  }

  open() {
    this.store.commit('menu/update', ['mobileOpen', true])
    document.body.appendChild(this.overlay);
  }

  close() {
    this.store.commit('menu/update', ['mobileOpen', false])
    document.body.removeChild(this.overlay);
  }

  toggle() {
    this.store.state.menu.mobileOpen
      ? this.close()
      : this.open()
  }

  on(event, callback) {
    EventBus.$on(event, callback);
  }

  off(event, callback) {
    EventBus.$off(event, callback);
  }
}
