<template>
  <nav class="stacked-menu" :class="classList">
    <ul class="menu">
      <menu-item v-for="(item, index) in items" :key="`${item.id}-${index}`" :item="item" :index="index" :path="`${index}`"></menu-item>

      <template v-if="marketplace && marketplace.length">
        <li class="menu-header">Marketplace</li>
        <menu-item  v-for="(item, index) in marketplace" :key="`${item.id}-${index}`" :item="item" :index="index" :path="`${index}`" source="marketplace"></menu-item>
      </template>

    </ul>
  </nav>
</template>

<script>
import MenuItem from './stacked-menu/MenuItem'
import { createNamespacedHelpers } from 'vuex'
const { mapState } = createNamespacedHelpers('menu')

export default {
  props: {
    items: {
      type: Array,
      required: true
    }
  },
  computed: {
    ...mapState({
      isMobile: state => state.mobile,
      isCollapsed: state => state.collapsed,
      marketplace: state => state.marketplace,
    }),
    classList() {
      return {
        'stacked-menu-has-compact': this.isCollapsed && !this.isMobile,
        'stacked-menu-has-hoverable': this.isCollapsed && !this.isMobile,
        'stacked-menu-has-collapsible': !this.isCollapsed || this.isMobile,
      }
    }
  },
  components: {
    MenuItem
  }
}
</script>
