<template>
  <div class="km__menu--wrapper" :class="{ show: isMobileOpen }">
    <aside class="km__menu--container" :style="containerStyle">
      <vue-scroll :ops="scrollOptions">
        <div class="km__menu--content">
          <stacked-menu :items="items"></stacked-menu>
        </div>
      </vue-scroll>

      <footer v-if="!isMobile" class="aside-footer border-top p-2">
        <button @click.prevent="toggleCollapse" class="btn btn-light btn-block text-primary" title="Collapse">
          <i :class="{ 'icon-double-angle-left': !isCollapsed, 'icon-double-angle-right': isCollapsed }"></i>
        </button>
      </footer>
    </aside>
  </div>
</template>

<script>
import VueScroll from 'vuescroll'
import StackedMenu from './components/StackedMenu'

import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('menu')

export default {
  data() {
    return {
      containerHeight: 45
    }
  },
  created() {
    this.handleContainerClass()
    this.handleResponsiveWidth()
    this.updateContainerHeight();

    window.addEventListener('resize', this.updateContainerHeight);
    document.addEventListener('scroll', this.updateContainerHeight);
  },
  computed: {
    ...mapState({
      items: state => state.items,
      isMobile: state => state.mobile,
      isMobileOpen: state => state.mobileOpen,
      isCollapsed: state => state.collapsed,
    }),
    scrollOptions() {
      return {
        scrollPanel: { scrollingX: false },
        bar: { background: '#74508f' }
      }
    },
    containerStyle() {
      const navbarHeight = 46;
      return {
        height: `${this.containerHeight - navbarHeight}px`,
      }
    }
  },
  methods: {
    ...mapActions([
      'toggleCollapse',
      'handleContainerClass',
      'handleResponsiveWidth',
    ]),
    updateContainerHeight() {
      this.containerHeight = window.innerHeight
    }
  },
  components: {
    VueScroll,
    StackedMenu
  }
}
</script>

<style lang="scss" src="./main.scss"></style>
