import axios from 'axios'
import qs from 'qs/lib/stringify'
import { hasWidthSmallerThan } from '../../utils/responsive'

export default data => {
  const initial = {
    items: [],
    shortcuts: [],
    marketplace: [],
    collapsed: false,
    hidden: false,
    fixed: false,
    mobile: false,
    mobileOpen: false,
  }

  const state = {
    ...initial,
    ...data
  }

  const mutations = {
    open(state, { path, source }) {
      if (typeof path === 'string' && path.length) {
        closeTree(state.items)
        closeTree(state.marketplace)
        let items = getMenuFromPath(path, state[source || 'items'])
        for (const index in items) {
          if (index < items.length - 1) {
            const menu = items[index];
            const item = items[parseInt(index) + 1];
            for (const child_index in menu.children) {
              const child = menu.children[child_index]
              child.open = (child.id == item.id);
              if (child.children && child.children.length) {
                child.children.forEach(sub => sub.open = false)
              }
            }
          } else {
            const item = items[index];
            item.open = true;
          }
        }
      }
    },
    close(state, { path, source }) {
      if (typeof path === 'string' && path.length) {
        const items = getMenuFromPath(path, state[source || 'items'])
        const item = items.pop();
        item.open = false;
      }
    },
    expand(state, { path, source }) {
      if (typeof path === 'string' && path.length) {
        let items = getMenuFromPath(path, state[source || 'items'])
        for (const index in items) {
          if (index < items.length - 1) {
            const menu = items[index];
            const item = items[parseInt(index) + 1];
            for (const child_index in menu.children) {
              const child = menu.children[child_index]
              child.expanded = (child.id == item.id);
              if (child.children && child.children.length) {
                child.children.forEach(sub => sub.expanded = false)
              }
            }
          } else {
            const item = items[index];
            item.expanded = true;
          }
        }
      }
    },
    collapse(state, { path, source }) {
      if (typeof path === 'string' && path.length) {
        const items = getMenuFromPath(path, state[source || 'items'])
        const item = items.pop();
        item.expanded = false;
      }
    },
    update(state, [prop, value]) {
      state[prop] = value
    },
    toggleCollapse(state) {
      closeTree(state.items)
      closeTree(state.marketplace)
      state.collapsed = !state.collapsed
      handleContainerClass(
        state.collapsed, state.mobile
      )
    }
  }
  const actions = {
    open ({ commit }, { path, source }) {
      commit('open', { path, source })
    },
    close ({ commit }, { path, source }) {
      commit('close', { path, source })
    },
    expand ({ commit }, { path, source }) {
      commit('expand', { path, source })
    },
    collapse ({ commit }, { path, source }) {
      commit('collapse', { path, source })
    },
    update ({ commit }, [prop, value]) {
      commit('update', [prop, value])
    },
    handleContainerClass({ state }) {
      handleContainerClass(state.collapsed, state.mobile)
    },
    handleResponsiveWidth({ state, commit }) {
      hasWidthSmallerThan(991, (value) => {
        if (state.mobile != value) {
          commit('update', ['mobile', value])
          handleContainerClass(state.collapsed, value)
        }
      })
    },
    async toggleCollapse({ commit, state }) {
      commit('toggleCollapse')
      axios.post(window.location.origin + '/crm/index.php/welcome/setMenuSize/', qs({
        size: state.collapsed ? 'small' : 'big'
      }))
    },
  }
  const getters = {}

  return {
    namespaced: true,
    state,
    actions,
    getters,
    mutations,
  }
}

const getMenuFromPath = (path, children) => {
  if (typeof path === 'string' && path.length) {
    let items = [{ children }];
    let indexes = path.split('.');

    let menu = items[0];
    while (indexes.length > 0) {
      let pos = parseInt(indexes.shift())
      menu = menu.children[pos]
      items.push(menu)
    }

    return items
  }
}

const closeTree = (items) => {
  for (const index in items) {
    const item = items[index];
    if (item.open) {
      item.open = false;
      if (item.children && item.children.length) {
        closeTree(item.children)
      }
    }
  }
}

const handleContainerClass = (collapsed, mobile) => {
  const container = document.querySelector('.main-container')
  collapsed && !mobile
    ? container.classList.add('has-compact-menu')
    : container.classList.remove('has-compact-menu')
  mobile
    ? container.classList.add('has-mobile-menu')
    : container.classList.remove('has-mobile-menu')
}
