<template>
  <span class="menu-link" :class="classList">
    <span v-if="floating == false" class="menu-link--action" @click="expand"></span>
    <a :href="link || '#'" @click="anchorClick" :target="item.target" :title="item.title">
      <slot></slot>
    </a>
  </span>
</template>

<script>
import hoverintent from 'hoverintent'
import { createNamespacedHelpers } from 'vuex'
import { EventBus } from '../../utils/event-bus';
const { mapState } = createNamespacedHelpers('menu')

export default {
  props: {
    item: {
      type: Object,
      required: true
    },
    depth: {
      type: Number,
      required: true
    },
    floating: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      hover: null
    }
  },
  mounted() {
    this.hover = hoverintent(this.$el, this.handleMouse, () => {}).options({
      sensitivity: 14,
      interval: 100,
    })
  },
  beforeDestroy() {
    if (this.hover) {
      this.hover.remove();
      this.hover = null
    }
  },
  computed: {
    ...mapState({
      isMobile: state => state.mobile,
      isCollapsed: state => state.collapsed,
    }),
    hasChildren() {
      return this.item.children.length > 0
    },
    classList() {
      return {
        'menu-link--create': this.item.create,
        'menu-link--overview': this.item.overview,
      }
    },
    link() {
      return this.item.url && this.item.url.trim().length > 0
        ? this.item.url
        : null
    },
    isFirstLevel() {
      return this.depth === 1
    },
    events() {
      return this.item.class && this.item.class.length
        ? this.item.class.split(' ')
        : null
    },
  },
  methods: {
    anchorClick(e) {
      if ((this.depth === 1 && this.hasChildren)|| (this.isMobile && this.hasChildren)) {
        e.preventDefault()
        e.stopPropagation()
        this.expand()
      } else if (this.isCollapsed && this.link) {
        if (this.events) {
          this.events.forEach(event => EventBus.$emit(event, {event: e, scope: this }));
        } else {
          // folow link
        }
      } else if (this.link === null) {
        e.preventDefault()
        e.stopPropagation()
        this.expand()
      } else {
        if (this.events) {
          this.events.forEach(event => EventBus.$emit(event, {event: e, scope: this }));
        } else {
          // folow link
        }
      }
    },
    expand() {
        this.$emit('expand')
    },
    handleMouse() {
      if (this.isMobile == false) {
        this.$emit('open')
      }
    }
  }
}
</script>
